html,
body {
  height: 100%;
  width: 100%;
}

body {
  margin: 0;
  min-width: min-content;

  #root {
    display: flex;
    flex-direction: column;
    min-height: 100%;

    .page-wrapper {
      display: flex;
      flex: 1 1 auto;
      flex-direction: column;
    }
  }
}
